<template>
  <v-flex pa-2 xs12 sm6>
    <v-card>
      <v-card-title class="pt-1" v-bind:class="{ 'pb-1': !optionSubtitle }">{{ optionTitle }}</v-card-title>
      <v-card-subtitle v-if="optionSubtitle" class="pb-1">{{ optionSubtitle }}</v-card-subtitle>
      <v-card-text class="pb-2">
        <v-checkbox
          v-model="isToggled"
          :label="toggleTitle"
          class="mt-0 mb-1"
          hide-details
          color="primary"
          @change="handleChange"
        ></v-checkbox>
        <multiselect
          :disabled="isToggled"
          v-model="selectedOptions"
          :options="optionList"
          :multiple="true"
          :close-on-select="false"
          :clear-on-select="false"
          :preserve-search="true"
          placeholder="Search..."
          @input="handleChange"
        ></multiselect>
      </v-card-text>
    </v-card>
  </v-flex>
</template>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "SearchOption",

  components: {
    Multiselect
  },

  props: {
    optionTitle: String,
    optionSubtitle: String,
    toggleTitle: String,
    optionList: Array,
    mutationName: String
  },

  data: () => ({
    isToggled: true,
    selectedOptions: []
  }),

  methods: {
    handleChange: function() {
      this.$store.commit({
        type: this.$props.mutationName,
        selectedOptions: this.isToggled || !this.selectedOptions.length ? null : this.selectedOptions
      });
    }
  }
};
</script>
