<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <div class="d-flex align-center">
        <h1>Spoonacular Menu Builder</h1>
      </div>

      <v-spacer></v-spacer>

      <v-btn icon x-large @click="setPage('search')" :class="{ 'selected-button': currentPage === 'search' }">
        <v-icon x-large>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon x-large @click="setPage('cart')" :class="{ 'selected-button': currentPage === 'cart' }">
        <v-badge :value="menuItemsCount" :content="menuItemsCount" color="green" overlap>
          <v-icon x-large>mdi-silverware</v-icon>
        </v-badge>
      </v-btn>
    </v-app-bar>

    <v-content>
      <SearchForm v-show="currentPage === 'search'" />
      <MenuCart v-show="currentPage === 'cart'" />
    </v-content>
  </v-app>
</template>

<style>
.selected-button {
  background-color: #ffffff66;
}
</style>

<script>
import SearchForm from "./components/SearchForm";
import MenuCart from "./components/MenuCart";
import { mapGetters } from "vuex";

export default {
  name: "App",

  components: {
    SearchForm,
    MenuCart
  },

  computed: {
    ...mapGetters(["menuItemsCount"])
  },

  methods: {
    setPage: function(page) {
      this.currentPage = page;
    }
  },

  data: () => ({
    currentPage: "search"
  })
};
</script>
