<template>
  <v-container>
    <v-layout row wrap justify-space-between>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn color="success" v-on="on">Actions</v-btn>
        </template>
        <v-list>
          <v-list-item :disabled="viewableItems.length == 0" @click="getAllNutrition">
            <v-list-item-title>Get All Nutrition</v-list-item-title>
          </v-list-item>
          <v-list-item :disabled="viewableItems.length == 0" @click="downloadJson">
            <v-list-item-title>Download Raw JSON</v-list-item-title>
          </v-list-item>
          <v-list-item :disabled="viewableItems.length == 0" @click="downloadShopify">
            <v-list-item-title>Download Shopify CSV</v-list-item-title>
          </v-list-item>
          <v-list-item :disabled="viewableItems.length == 0" @click="downloadJolt">
            <v-list-item-title>Download Jolt CSV</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-dialog v-model="actionDialog" persistent max-width="400">
        <v-card>
          <v-card-title class="headline justify-center">{{ actionDialogTitle }}</v-card-title>
          <v-card-text class="text-center">
            <v-progress-circular :size="100" :width="15" :value="actionProgress" color="teal"></v-progress-circular>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-spacer></v-spacer>
      <v-dialog v-model="clearDialog" persistent max-width="290">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark outlined v-on="on">Clear Menu</v-btn>
        </template>
        <v-card>
          <v-card-title class="headline">Are you sure you want to clear the menu?</v-card-title>
          <v-card-actions>
            <v-btn color="green darken-1" text @click="clearDialog = false">Cancel</v-btn>
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="clearCart">Clear it!</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <v-layout row wrap justify-space-between>
      <v-flex mt-3 xs12>
        <v-data-table
          :headers="headers"
          :items="viewableItems"
          :items-per-page="1000"
          item-key="id"
          class="elevation-1 narrower-columns"
          disable-sort
          hide-default-footer
          show-expand
        >
          <template v-slot:item.action="{ item }">
            <v-icon large @click="removeMenuItem({ item: item })">mdi-cart-remove</v-icon>
          </template>
          <template v-slot:item.image="{ item }">
            <v-img
              :src="`https://spoonacular.com/recipeImages/${item.id}-90x90.${item.imageType}`"
              max-width="90"
            ></v-img>
          </template>
          <template v-slot:item.preparationMinutes="{ item }">{{ item.preparationMinutes | showDash }}</template>
          <template v-slot:item.cookingMinutes="{ item }">{{ item.cookingMinutes | showDash }}</template>
          <template v-slot:item.readyInMinutes="{ item }">{{ item.readyInMinutes | showDash }}</template>
          <template v-slot:item.stepCount="{ item }">
            {{
              item.analyzedInstructions
                ? item.analyzedInstructions
                    .map(instructions => instructions.steps.length)
                    .reduce((acc, stepCount) => acc + stepCount, 0)
                : "N/A"
            }}
          </template>
          <template v-slot:item.sourceUrl="{ item }">
            <a :href="item.sourceUrl">{{ item.sourceUrl | getHostname }}</a>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" class="pa-4">
              <RecipeDetails :recipe="item" />
            </td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<style>
.narrower-columns td:not(:first-child):not(:last-child) {
  padding: 0 8px;
}
.narrower-columns td:first-child {
  padding-left: 16px;
  padding-right: 8px;
}
.narrower-columns td:last-child {
  padding-left: 8px;
  padding-right: 16px;
}
</style>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { saveAs } from "file-saver";
import Papa from "papaparse";
import RecipeDetails from "./RecipeDetails";

export default {
  name: "MenuCart",

  components: {
    RecipeDetails
  },

  computed: {
    ...mapState(["menuItems", "nutritionCache"]),
    viewableItems: function() {
      return Object.values(this.menuItems);
    }
  },

  methods: {
    ...mapActions(["getNutrition"]),
    ...mapMutations(["removeMenuItem", "clearMenuItems"]),
    clearCart: function() {
      this.clearDialog = false;
      this.clearMenuItems();
    },
    getAllNutrition: async function() {
      this.actionDialogTitle = "Retrieving all nutrition info";
      this.actionProgress = 0;
      this.actionDialog = true;

      let actionItemsProcessed = 0;

      // for everything in the cart, try to get nutrition info
      for (const recipe of this.viewableItems) {
        await this.getNutrition(recipe.id);
        actionItemsProcessed++;
        this.actionProgress = (actionItemsProcessed / this.viewableItems.length) * 100;
      }

      // brief wait to actually see the list finish
      await new Promise(resolve => setTimeout(resolve, 750));

      this.actionDialog = false;
    },
    downloadJson: async function() {
      this.actionDialogTitle = "Preparing JSON download";
      this.actionDialog = true;

      let actionItemsProcessed = 0;

      // we want a clone of the cart item data (only) so use a json approach to clone it
      let clonedData = JSON.parse(JSON.stringify(this.viewableItems));

      // now loop all the items, make sure we have nutrition, then add the nutrition to the item itself
      for (const recipe of clonedData) {
        await this.getNutrition(recipe.id);
        recipe.nutrients = JSON.parse(JSON.stringify(this.nutritionCache[recipe.id].nutrients));

        actionItemsProcessed++;
        this.actionProgress = (actionItemsProcessed / this.viewableItems.length) * 100;
      }

      // construct the actual file save
      saveAs(
        new Blob([JSON.stringify(clonedData)], { type: "text/json" }),
        `raw_${Math.floor(Date.now() / 1000)}.json`
      );

      // brief wait to actually see the list finish
      await new Promise(resolve => setTimeout(resolve, 750));

      this.actionDialog = false;
    },
    getSpecificNutritionInfo: function(nutritionData, searchTitle) {
      // search the nutrition array for the given title
      let foundItems = nutritionData.filter(nutrient => nutrient.title.toLowerCase() == searchTitle.toLowerCase());
      if (foundItems.length === 0) {
        // nothing found, return placeholders
        return {
          amount: 0,
          unit: "-",
          percentDV: 0
        };
      }

      // found something, so return the values
      return {
        amount: foundItems[0].amount,
        unit: foundItems[0].unit,
        percentDV: foundItems[0].percentOfDailyNeeds
      };
    },
    downloadJolt: async function() {
      this.actionDialogTitle = "Preparing Jolt download";
      this.actionDialog = true;

      let actionItemsProcessed = 0;

      // we'll build a new array of items that'll become the rows of the CSV
      let csvData = new Array();

      // now loop all the items, make sure we have nutrition, then add the nutrition to the item itself
      for (const recipe of this.viewableItems) {
        await this.getNutrition(recipe.id);

        let nutrients = this.nutritionCache[recipe.id].nutrients;

        // build the row
        let row = Object();
        row["Label Name"] = recipe.title; // REQUIRED

        row["Category"] = "UNKNOWN"; // REQUIRED, don't know what to set

        row["Serving Size"] = "0"; // REQUIRED, don't see it in data

        row["Servings Per Container"] = "0"; // no idea what to set

        row["Calories"] = this.getSpecificNutritionInfo(nutrients, "Calories").amount; // REQUIRED

        row["Calories from Fat"] = "0"; // REQUIRED, don't see it in the data

        let fat = this.getSpecificNutritionInfo(nutrients, "Fat");
        row["Total Fat"] = fat.amount;
        row["Total Fat - Units"] = fat.unit;
        row["Total Fat - %DV"] = fat.percentDV;

        let satFat = this.getSpecificNutritionInfo(nutrients, "Fat");
        row["Saturated Fat"] = satFat.amount;
        row["Saturated Fat - Units"] = satFat.unit;
        row["Saturated Fat - %DV"] = satFat.percentDV;

        let transFat = this.getSpecificNutritionInfo(nutrients, "Trans Fat");
        row["Trans Fat"] = transFat.amount;
        row["Trans Fat - Units"] = transFat.unit;
        row["Trans Fat - %DV"] = transFat.percentDV;

        let cholesterol = this.getSpecificNutritionInfo(nutrients, "Cholesterol");
        row["Cholesterol"] = cholesterol.amount;
        row["Cholesterol - Units"] = cholesterol.unit;
        row["Cholesterol - %DV"] = cholesterol.percentDV;

        let sodium = this.getSpecificNutritionInfo(nutrients, "Sodium");
        row["Sodium"] = sodium.amount;
        row["Sodium - Units"] = sodium.unit;
        row["Sodium - %DV"] = sodium.percentDV;

        let carbohydrates = this.getSpecificNutritionInfo(nutrients, "Carbohydrates");
        row["Total Carbohydrates"] = carbohydrates.amount;
        row["Total Carbohydrates - Units"] = carbohydrates.unit;
        row["Total Carbohydrates - %DV"] = carbohydrates.percentDV;

        let fiber = this.getSpecificNutritionInfo(nutrients, "Fiber");
        row["Dietary Fiber"] = fiber.amount;
        row["Dietary Fiber - Units"] = fiber.unit;
        row["Dietary Fiber - %DV"] = fiber.percentDV;

        let sugar = this.getSpecificNutritionInfo(nutrients, "Sugar");
        row["Total Sugars"] = sugar.amount;
        row["Total Sugars - Units"] = sugar.unit;
        row["Total Sugars - %DV"] = sugar.percentDV;

        let protein = this.getSpecificNutritionInfo(nutrients, "Protein");
        row["Protein"] = protein.amount;
        row["Protein - Units"] = protein.unit;
        row["Protein - %DV"] = protein.percentDV;

        let vitA = this.getSpecificNutritionInfo(nutrients, "Vitamin A");
        row["Vitamin A"] = vitA.amount;
        row["Vitamin A - Units"] = vitA.unit;
        row["Vitamin A - %DV"] = vitA.percentDV;

        let vitC = this.getSpecificNutritionInfo(nutrients, "Vitamin C");
        row["Vitamin C"] = vitC.amount;
        row["Vitamin C - Units"] = vitC.unit;
        row["Vitamin C - %DV"] = vitC.percentDV;

        let calcium = this.getSpecificNutritionInfo(nutrients, "Calcium");
        row["Calcium"] = calcium.amount;
        row["Calcium - Units"] = calcium.unit;
        row["Calcium - %DV"] = calcium.percentDV;

        let iron = this.getSpecificNutritionInfo(nutrients, "Iron");
        row["Iron"] = iron.amount;
        row["Iron - Units"] = iron.unit;
        row["Iron - %DV"] = iron.percentDV;

        let combinedIngredients = [...recipe.usedIngredients, ...recipe.missedIngredients].map(
          ingredient => ingredient.name
        );
        row["Ingredients"] = combinedIngredients.join();

        row["Expiration"] = "-"; // REQUIRED, not found in data
        row["Expiration - Units"] = "-"; // REQUIRED, not found in data

        csvData.push(row);

        actionItemsProcessed++;
        this.actionProgress = (actionItemsProcessed / this.viewableItems.length) * 100;
      }

      let csvString = Papa.unparse(csvData, { quotes: true });

      // construct the actual file save
      saveAs(new Blob([csvString], { type: "text/csv" }), `jolt_${Math.floor(Date.now() / 1000)}.csv`);

      // brief wait to actually see the list finish
      await new Promise(resolve => setTimeout(resolve, 750));

      this.actionDialog = false;
    },
    downloadShopify: async function() {
      this.actionDialogTitle = "Preparing Shopify download";
      this.actionDialog = true;

      let actionItemsProcessed = 0;

      // we'll build a new array of items that'll become the rows of the CSV
      let csvData = new Array();

      // now loop all the items, make sure we have nutrition, then add the nutrition to the item itself
      for (const recipe of this.viewableItems) {
        await this.getNutrition(recipe.id);

        // build the row
        let row = Object();
        row["Handle"] = recipe.id;
        row["Title"] = recipe.title;
        row["Body (HTML)"] = "";
        row["Vendor"] = "Cheesus Christ";
        row["Type"] = "Meal";
        row["Tags"] = "";
        row["Published"] = "TRUE";
        row["Option1 Name"] = "Title";
        row["Option1 Value"] = "Default Title";
        row["Option2 Name"] = "";
        row["Option2 Value"] = "";
        row["Option3 Name"] = "";
        row["Option3 Value"] = "";
        row["Variant SKU"] = "";
        row["Variant Grams"] = "";
        row["Variant Inventory Tracker"] = "shopify";
        row["Variant Inventory Qty"] = "";
        row["Variant Inventory Policy"] = "deny";
        row["Variant Fulfillment Service"] = "manual";
        row["Variant Price"] = "";
        row["Variant Compare At Price"] = "";
        row["Variant Requires Shipping"] = "TRUE";
        row["Variant Taxable"] = "TRUE";
        row["Variant Barcode"] = "";
        row["Image Src"] = recipe.image;
        row["Image Position"] = "1";
        row["Image Alt Text"] = "";
        row["Gift Card"] = "";
        row["SEO Title"] = "";
        row["SEO Description"] = "";
        row["Google Shopping / Google Product Category"] = "";
        row["Google Shopping / Gender"] = "";
        row["Google Shopping / Age Group"] = "";
        row["Google Shopping / MPN"] = "";
        row["Google Shopping / AdWords Grouping"] = "";
        row["Google Shopping / AdWords Labels"] = "";
        row["Google Shopping / Condition"] = "";
        row["Google Shopping / Custom Product"] = "";
        row["Google Shopping / Custom Label 0"] = "";
        row["Google Shopping / Custom Label 1"] = "";
        row["Google Shopping / Custom Label 2"] = "";
        row["Google Shopping / Custom Label 3"] = "";
        row["Google Shopping / Custom Label 4"] = "";
        row["Variant Image"] = "";
        row["Variant Weight Unit"] = "";
        row["Variant Tax Code"] = "";
        row["Cost per item"] = "";

        csvData.push(row);

        actionItemsProcessed++;
        this.actionProgress = (actionItemsProcessed / this.viewableItems.length) * 100;
      }

      let csvString = Papa.unparse(csvData, { quotes: true });

      // construct the actual file save
      saveAs(new Blob([csvString], { type: "text/csv" }), `shopify_${Math.floor(Date.now() / 1000)}.csv`);

      // brief wait to actually see the list finish
      await new Promise(resolve => setTimeout(resolve, 750));

      this.actionDialog = false;
    }
  },

  filters: {
    getHostname: function(value) {
      if (!value) return "";
      return new URL(value).hostname;
    },
    showDash: function(value) {
      return value ? value : "-";
    }
  },

  data: () => ({
    clearDialog: false,
    actionDialog: false,
    actionDialogTitle: "",
    actionProgress: 0,
    isLoading: false,
    headers: [
      { text: "", value: "action" },
      {
        align: "left",
        value: "image"
      },
      {
        text: "ID",
        align: "left",
        sortable: false,
        value: "id",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TITLE",
        align: "left",
        sortable: false,
        value: "title",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "PREP TIME",
        align: "left",
        sortable: false,
        value: "preparationMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "COOK TIME",
        align: "left",
        sortable: false,
        value: "cookingMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "READY IN",
        align: "left",
        sortable: false,
        value: "readyInMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TOTAL STEPS",
        align: "left",
        sortable: false,
        value: "stepCount",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "SOURCE",
        align: "left",
        sortable: false,
        value: "sourceUrl",
        class: ["font-weight-black", "subtitle-3"]
      },
      { text: "", value: "data-table-expand" }
    ]
  })
};
</script>
