<template>
  <v-flex mt-3 xs12 v-if="lastResults">
    <v-data-table
      v-model="selectedSearchResults"
      :headers="headers"
      :items="lastResults"
      :items-per-page="resultsPerPage"
      :loading="searching"
      item-key="id"
      show-select
      class="elevation-1 narrower-columns"
      disable-sort
      hide-default-footer
      show-expand
      @item-selected="handleSelection"
      @toggle-select-all="handleSelectAll"
    >
      <template v-slot:item.image="{ item }">
        <v-img :src="`https://spoonacular.com/recipeImages/${item.id}-90x90.${item.imageType}`" max-width="90"></v-img>
      </template>
      <template v-slot:item.preparationMinutes="{ item }">
        {{ item.preparationMinutes | showDash }}
      </template>
      <template v-slot:item.cookingMinutes="{ item }">
        {{ item.cookingMinutes | showDash }}
      </template>
      <template v-slot:item.readyInMinutes="{ item }">
        {{ item.readyInMinutes | showDash }}
      </template>
      <template v-slot:item.stepCount="{ item }">
        {{
          item.analyzedInstructions
            ? item.analyzedInstructions
                .map(instructions => instructions.steps.length)
                .reduce((acc, stepCount) => acc + stepCount, 0)
            : "N/A"
        }}
      </template>
      <template v-slot:item.sourceUrl="{ item }">
        <a :href="item.sourceUrl">
          {{ item.sourceUrl | getHostname }}
        </a>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="pa-4">
          <RecipeDetails :recipe="item" />
        </td>
      </template>
    </v-data-table>
    <div class="text-center pt-2">
      <v-pagination
        :value="lastResultsPage"
        :length="lastResultsPageCount"
        :total-visible="10"
        @input="runQuery"
      ></v-pagination>
    </div>
  </v-flex>
</template>

<style>
.narrower-columns td:not(:first-child):not(:last-child) {
  padding: 0 8px;
}
.narrower-columns td:first-child {
  padding-left: 16px;
  padding-right: 8px;
}
.narrower-columns td:last-child {
  padding-left: 8px;
  padding-right: 16px;
}
</style>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import RecipeDetails from "./RecipeDetails";

export default {
  name: "SearchResults",

  components: {
    RecipeDetails
  },

  computed: {
    ...mapState([
      "searching",
      "resultsPerPage",
      "lastResults",
      "lastTotalResults",
      "lastOffset",
      "lastSearchUrl",
      "lastResultsPage",
      "menuItems"
    ]),
    ...mapGetters(["lastResultsPageCount"]),
    selectedSearchResults: {
      get() {
        return Object.values(this.menuItems);
      },
      set() {
        /* noop, because I already handle the toggle events */
      }
    }
  },

  methods: {
    ...mapActions(["runQuery"]),
    ...mapMutations(["addMenuItem", "removeMenuItem"]),
    handleSelection: function({ item, value: isSelected }) {
      isSelected ? this.addMenuItem({ item: item }) : this.removeMenuItem({ item: item });
    },
    handleSelectAll: function({ items, value }) {
      items.forEach(item => {
        this.handleSelection({ item, value });
      });
    }
  },

  filters: {
    getHostname: function(value) {
      if (!value) return "";
      return new URL(value).hostname;
    },
    showDash: function(value) {
      return value ? value : "-";
    }
  },

  data: () => ({
    isLoading: false,
    headers: [
      {
        align: "left",
        value: "image"
      },
      {
        text: "ID",
        align: "left",
        sortable: false,
        value: "id",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TITLE",
        align: "left",
        sortable: false,
        value: "title",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "PREP TIME",
        align: "left",
        sortable: false,
        value: "preparationMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "COOK TIME",
        align: "left",
        sortable: false,
        value: "cookingMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "READY IN",
        align: "left",
        sortable: false,
        value: "readyInMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TOTAL STEPS",
        align: "left",
        sortable: false,
        value: "stepCount",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "SOURCE",
        align: "left",
        sortable: false,
        value: "sourceUrl",
        class: ["font-weight-black", "subtitle-3"]
      },
      { text: "", value: "data-table-expand" }
    ]
  })
};
</script>
