<template>
  <v-container>
    <h3>Ingredients</h3>
    <h4>Makes {{ recipe.servings }} serving{{ recipe.servings > 1 ? "s" : "" }}</h4>
    <ul v-if="recipe.usedIngredients || recipe.missedIngredients">
      <li v-for="(ingredient, index) in [...recipe.usedIngredients, ...recipe.missedIngredients]" :key="index">
        {{ ingredient.original }}
      </li>
    </ul>
    <div v-if="!recipe.usedIngredients && !recipe.missedIngredients">Data missing</div>
    <h3>Steps</h3>
    <ul v-if="recipe.analyzedInstructions">
      <li
        v-for="(step, index) in [].concat(
          ...recipe.analyzedInstructions.map(group => [].concat(...group.steps).map(step => step.step))
        )"
        :key="index"
      >
        {{ step }}
      </li>
    </ul>
    <div v-if="!recipe.analyzedInstructions">Data missing</div>
    <v-btn
      v-if="!nutritionCache[recipe.id]"
      x-large
      color="success"
      :disabled="isLoading"
      :loading="isLoading"
      @click="handleGetNutrients(recipe.id)"
      >Get Nutrition</v-btn
    >
    <h3 v-if="nutritionCache[recipe.id]">Nutrients</h3>
    <ul v-if="nutritionCache[recipe.id]" class="two-column-list">
      <li v-for="(nutrient, index) in specialSort(nutritionCache[recipe.id].nutrients)" :key="index">
        {{ nutrient.title }}: {{ nutrient.amount }} {{ nutrient.unit }}
      </li>
    </ul>
  </v-container>
</template>

<style>
.two-column-list {
  -webkit-column-count: 2; /* Chrome, Safari, Opera */
  -moz-column-count: 2; /* Firefox */
  column-count: 2;
}
</style>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "RecipeDetails",

  props: {
    recipe: Object
  },

  computed: {
    ...mapState(["menuItems", "nutritionCache"]),
    viewableItems: function() {
      return Object.values(this.menuItems);
    }
  },

  methods: {
    ...mapActions(["getNutrition"]),
    handleGetNutrients: function(recipeId) {
      this.isLoading = true;

      this.getNutrition(recipeId).then(() => {
        this.isLoading = false;
      });
    },
    specialSort: function(array) {
      let clone = [...array];
      clone.sort((a, b) => {
        let lowerAtitle = a.title.toLowerCase();
        let lowerBtitle = b.title.toLowerCase();

        // calories will always come first, but if both contain it do the normal sort
        if (lowerAtitle.includes("calories") && !lowerBtitle.includes("calories")) {
          return -1;
        }
        if (!lowerAtitle.includes("calories") && lowerBtitle.includes("calories")) {
          return 1;
        }

        // fats next
        if (lowerAtitle.includes("fat") && !lowerBtitle.includes("fat")) {
          return -1;
        }
        if (!lowerAtitle.includes("fat") && lowerBtitle.includes("fat")) {
          return 1;
        }

        // carbs next
        if (lowerAtitle.includes("carb") && !lowerBtitle.includes("carb")) {
          return -1;
        }
        if (!lowerAtitle.includes("carb") && lowerBtitle.includes("carb")) {
          return 1;
        }

        // sugar next
        if (lowerAtitle.includes("sugar") && !lowerBtitle.includes("sugar")) {
          return -1;
        }
        if (!lowerAtitle.includes("sugar") && lowerBtitle.includes("sugar")) {
          return 1;
        }

        // fiber next
        if (lowerAtitle.includes("fiber") && !lowerBtitle.includes("fiber")) {
          return -1;
        }
        if (!lowerAtitle.includes("fiber") && lowerBtitle.includes("fiber")) {
          return 1;
        }

        // protein next
        if (lowerAtitle.includes("protein") && !lowerBtitle.includes("protein")) {
          return -1;
        }
        if (!lowerAtitle.includes("protein") && lowerBtitle.includes("protein")) {
          return 1;
        }

        // all the vitamins go at the end
        if (lowerAtitle.includes("vitamin") && !lowerBtitle.includes("vitamin")) {
          return 1;
        }
        if (!lowerAtitle.includes("vitamin") && lowerBtitle.includes("vitamin")) {
          return -1;
        }

        return lowerAtitle >= lowerBtitle ? 1 : -1;
      });

      return clone;
    }
  },

  data: () => ({
    isLoading: false
  })
};
</script>
