<template>
  <v-container>
    <v-layout row wrap justify-space-between>
      <v-flex pa-2 xs12>
        <v-card>
          <v-card-title class="pt-1">API Key</v-card-title>
          <v-card-subtitle class="pb-1">Enter your Spoonacular API key</v-card-subtitle>
          <v-card-text class="pb-2">
            <v-text-field v-model="apiKey" dense hide-details placeholder="API key" outlined @input="handleApi" />
          </v-card-text>
        </v-card>
      </v-flex>

      <SearchOption
        optionTitle="Diet"
        optionSubtitle="Select 'Any', or choose 1 or more available diets from list"
        toggleTitle="Any"
        :optionList="dietOptions"
        mutationName="setDiets"
      />

      <SearchOption
        optionTitle="Cuisine"
        optionSubtitle="Select 'Any', or choose 1 or more available cuisines from list"
        toggleTitle="Any"
        :optionList="cuisineOptions"
        mutationName="setCuisines"
      />

      <SearchOption
        optionTitle="Intolerances"
        optionSubtitle="Select 'None', or choose 1 or more available intolerances from list"
        toggleTitle="None"
        :optionList="intoleranceOptions"
        mutationName="setIntolerances"
      />

      <SearchOption
        optionTitle="Meal Types"
        optionSubtitle="Select 'Any', or choose 1 or more available meal types from list"
        toggleTitle="Any"
        :optionList="mealOptions"
        mutationName="setMeals"
      />

      <v-flex pa-2 xs12 sm6>
        <v-card>
          <v-card-title class="pt-1">Ingredients</v-card-title>
          <v-card-subtitle class="pb-1">Enter ingredients that must be in the returned recipes</v-card-subtitle>
          <v-card-text class="pb-2">
            <multiselect
              v-model="ingredients"
              :options="[]"
              :multiple="true"
              :close-on-select="false"
              :clear-on-select="true"
              placeholder="Add..."
              :allow-empty="true"
              :show-labels="false"
              taggable
              tagPlaceholder="Press enter to add"
              @tag="addTag"
              @input="handleIngredients"
            >
              <template v-slot:noOptions>Begin typing to add ingredient</template>
            </multiselect>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex pa-2 xs12 sm6>
        <v-card>
          <v-card-title class="pt-1 pb-1">Sorting</v-card-title>
          <v-card-text class="pb-2">
            <multiselect
              v-model="sortSelection"
              :options="sortOptions"
              :multiple="false"
              :close-on-select="true"
              :clear-on-select="false"
              :preserve-search="false"
              placeholder="Search..."
              :allow-empty="false"
              :show-labels="false"
              @input="handleSort"
            ></multiselect>

            <v-btn-toggle
              v-model="sortDirection"
              borderless
              mandatory
              dense
              class="mt-1"
              color="primary"
              @change="handleSort"
            >
              <v-btn value="asc">
                <span>ASC</span>
                <v-icon right>mdi-sort-ascending</v-icon>
              </v-btn>
              <v-btn value="desc">
                <span>DESC</span>
                <v-icon right>mdi-sort-descending</v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex pa-2 xs12>
        <v-btn x-large color="success" :disabled="searching || !apiKey" :loading="searching" @click="runQuery(1)"
          >Search</v-btn
        >
      </v-flex>

      <SearchResults />
    </v-layout>
  </v-container>
</template>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>

<script>
import SearchOption from "./SearchOption";
import SearchResults from "./SearchResults";
import { mapGetters, mapState, mapActions } from "vuex";
import Multiselect from "vue-multiselect";

export default {
  name: "SearchForm",

  components: {
    SearchOption,
    SearchResults,
    Multiselect
  },

  methods: {
    ...mapActions(["runQuery"]),
    handleApi: function() {
      this.$store.commit({
        type: "setApiKey",
        apiKey: this.apiKey
      });
    },
    handleSort: function() {
      this.$store.commit({
        type: "setSorting",
        sort: this.sortSelection,
        sortDirection: this.sortDirection
      });
    },
    handleIngredients: function() {
      this.$store.commit({
        type: "setIngredients",
        selectedOptions: this.ingredients.length ? this.ingredients : null
      });
    },
    addTag: function(newTag) {
      let tag = newTag.trim().replace(",", "");
      if (!this.ingredients.some(element => element.toLowerCase() === tag)) {
        // don't push, it'll break state
        this.ingredients = [...this.ingredients, tag];
        this.handleIngredients();
      }
    }
  },

  computed: {
    ...mapGetters(["dietOptions", "cuisineOptions", "intoleranceOptions", "mealOptions", "sortOptions"]),
    ...mapState(["searching"])
  },

  data: () => ({
    apiKey: "",
    ingredients: [],
    sortSelection: "default",
    sortDirection: "asc"
  })
};
</script>
